.image-wrapper {
  height: 250px;
  background-color: #212121;
  border-radius: 15px;
  overflow: hidden;
}

.image-wrapper > img {
  width: 100%;
  height: auto;
}

.info-block {
  margin: 0 auto;
  width: 95%;
}

.image-container {
  position: relative;
}

.merken,
.entmerken {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
